import * as React from 'react';
import {Link, Typography, Box, Stepper, Step, StepLabel, StepContent, Button} from '@material-ui/core';


const steps = [
  {
    label: 'Crea tu cuenta.',
    description: `Registrase en SolSystems es muy fcil, solo te llevar unos minutos. 
    Eso s, para garantizar tu seguridad y la del resto de usuarios, 
    debers validar cierta informacin, como tu DNI, antes de realizar tu primera inversin.`,
  },
  {
    label: 'Invierte en sostenibilidad.',
    description:
      `Ingresa fondos en tu wallet de Lemon Way y preprate a invertir! 
      Tmate tu tiempo para conocer las caractersticas de las oportunidades publicadas 
      y consulta los anlisis realizados por el equipo de SolSystems.`,
  },
  {
    label: 'Recibe los beneficios.',
    description: `Desde la plataforma, podrs seguir el da a da de los proyectos y 
    monitorizar en tiempo real tus inversiones. Recibirs peridicamente los beneficios, 
    que podrs retirar o reinvertir en tu portfolio sostenible.`,
  },
  {
    label: 'Compra o vende acciones. ',
    description: `El tabln de anuncios facilita la compraventa de acciones y ttulos 
    de deuda entre los usuarios de la plataforma. Esta funcionalidad te permitir deshacerte 
    de parte de tu portfolio o ampliarlo!`,
  }
];

const Home = () => {
  const [activeStep, setActiveStep] = React.useState(0);

  const handleNext = () => {
    setActiveStep((prevActiveStep) => prevActiveStep + 1);
  };

  const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
  };

  return (
      <div>
    <Typography variant="h2" gutterBottom component="div">
    Invierte fcil.
  </Typography>
    <Box sx={{ maxWidth: "60%" }}>
      <Stepper activeStep={activeStep} orientation="vertical">
        {steps.map((step, index) => (
          <Step key={step.label}>
            <StepLabel
              optional={
                index === 3 ? (
                  <Typography variant="caption">Last step</Typography>
                ) : null
              }
            >
                <Typography variant="h4" gutterBottom component="div">
                {step.label}
                </Typography>
            </StepLabel>
            <StepContent>
              <Typography>{step.description}</Typography>
              <Box sx={{ mb: 2 }}>
                <div>
                  <Button
                    variant="contained"
                    onClick={handleNext}
                    sx={{ mt: 1, mr: 1 }}
                  >
                    {index === steps.length - 1 ? 'Finish' : 'Continue'}
                  </Button>
                  <Button
                    disabled={index === 0}
                    onClick={handleBack}
                    sx={{ mt: 1, mr: 1 }}
                  >
                    Back
                  </Button>
                </div>
              </Box>
            </StepContent>
          </Step>
        ))}
      </Stepper>
      <div  style={{textAlign: "center"}}> 
      <Link href="/signup" variant="body2">
        <Button  variant="contained" color="success">
          REGISTRARME
        </Button>
      </Link>
       </div>
    </Box>
    <Typography style={{padding:"5%"}} variant="h2" gutterBottom component="div">
        En financiacin.
    </Typography>
    </div>
  );
}




export default Home;